  background-color: white;
  display: flex;
  justify-content: space-between;
}

.cartTop__remove {
  color: white;
  background-color: firebrick;
  cursor: pointer;
}

.cartTop__total {
  border: 0px solid green;
  padding: 10px;
  margin-top: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: white;
}

.cartTop__finish {
  padding: 10px;
  width: 100%;
  font-size: 1.0em;
  cursor: pointer;
  background: firebrick;
  color: white;
  font-weight: bold;
}

@media screen and (max-width: 600px) {
  .cart {
    display: none;
  }

  .cartTop {
    display: block;
  }
}
* { 
  margin: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
}

html {
  border: 0px solid tomato;
}

body {
  border: 0px solid green; 
  font-family: 'Patrick Hand', sans-serif;      
}

.container {  
  display: flex;
  align-items: flex-start;
  justify-content: space-between;  
}

.content {
  border: 0px solid royalblue;
  width: 100%;
}


/* PACE LOADING */
.pace {
  -webkit-pointer-events: none;
  pointer-events: none;

  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
      user-select: none;

  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  transform: translate3d(0, -50px, 0);
  transition: transform .5s ease-out;
}

.pace.pace-active {
  transform: translate3d(0, 0, 0);
}

.pace .pace-progress {
  display: block;
  position: fixed;
  z-index: 2000;
  top: 0;
  right: 100%;
  width: 100%;
  height: 10px;
  background: #b72219;

  pointer-events: none;
}

